import React, { Fragment, useEffect, useState, useRef, Suspense } from "react";
import { Table, Form, Col, Overlay, Popover, Spinner } from "react-bootstrap";
import { Field } from "redux-form";
import ThemeButton from "../../button/ThemeButton";
import cuid from "cuid";
import RadioInput from "../RadioInput";
import { getListingDetailsById } from "../../../modules/listings/_crud/listingsCrud";
import moment from "moment";
import DefaultModal from "../../modal/DefaultModal";
import AddReport from "../../../modules/listings/modals/addReport/AddReport";

function Row({ fields, item, index, onClick }) {
  const [open, setOpen] = React.useState(false);

  const handleCollapse = (e) => {
    e.stopPropagation();
    setOpen(!open);
  };

  return (
    <React.Fragment>
      <tr onClick={() => onClick(index)}>
        <td>
          <Field
            name={`${item}.isSelected`}
            component={RadioInput}
            type="checkbox"
          />
        </td>
        <td>
          {fields?.get(index)?.report?.title}{" "}
          {fields?.get(index)?.report.isReportUploaded ? (
            <ThemeButton icon>
              {" "}
              <i class="fas fa-file    "></i>
            </ThemeButton>
          ) : (
            ""
          )}
        </td>
        <td>{moment(fields?.get(index)?.date).format("DD/MM/YYYY")}</td>
        <td>{fields?.get(index)?.preparedBy}</td>

        <td>{fields?.get(index)?.requestedBy}</td>
        <td>
          {fields?.get(index)?.ins ? (
            <span class="dot green"></span>
          ) : (
            <span class="dot red"></span>
          )}
        </td>
        <td className="d-flex">
          {fields?.get(index)?.rjp ? (
            <span className="dot green m-3"></span>
          ) : (
            <span className="dot red m-3"></span>
          )}{" "}
          {fields?.get(index)?.note ? (
            <span>
              <ThemeButton onClick={(e) => handleCollapse(e)} icon>
                {" "}
                <i class="fa fa-arrow-right" aria-hidden="true"></i>
              </ThemeButton>
            </span>
          ) : (
            ""
          )}
        </td>
      </tr>
      {fields?.get(index)?.note && open ? (
        <Fragment>
          <tr>
            <td className="p-5" colspan="7">
              {fields?.get(index)?.note}
            </td>
          </tr>
        </Fragment>
      ) : (
        ""
      )}
    </React.Fragment>
  );
}

const FormReports = (props) => {
  const { fields } = props;
  const [featuresValue, setFeaturesValue] = useState("");
  const [modalShow, setModalShow] = useState(false);
  const [id, setId] = useState(null);

  useEffect(() => {
    getListingDetailsById()
      .then(({ data }) => {
        if (!fields.length > 0) {
          data.map((item, index) => {
            fields.push(item);
          });
        }
      })
      .catch(() => {});
    // fields.push({
    //   id: false,
    //   isSelected: false,
    //   role: "bedrooms",
    //   name: "",
    //   phone: "4567890-",
    //   email: "asdsa@asda.com",
    // });
    // fields.push({
    //   id: false,
    //   isSelected: false,
    //   role: {
    //     label: "Seller Solicitor",
    //     value: "sellerSolicitor",
    //   },
    // });
    // fields.push({ id: false, isSelected: false, role: "seller" });
    // fields.push({
    //   id: false,
    //   isSelected: false,
    //   role: {
    //     label: "Sarah",
    //     value: "3",
    //   },
    // });
  }, []);

  useEffect(() => {
    handleRemove();
  }, [props.deleteItem]);

  const handleAddFeature = () => {
    let obj = {};
    obj.features = document.getElementById("features").value;
    obj.comments = document.getElementById("featureComments").value;
    obj.preference = document.getElementById("featurePreference")?.value
      ? document.getElementById("featurePreference").value
      : "";
    obj.landSize = document.getElementById("featureLandsize")?.value
      ? document.getElementById("featureLandsize").value
      : "";
    obj.landType = document.getElementById("featuresLandType")?.value
      ? document.getElementById("featuresLandType").value
      : "";
    fields.push(obj);
  };

  const handleRemove = () => {
    for (let i = fields.length - 1; i >= 0; i--) {
      if (fields.get(i).isSelected) {
        fields.remove(i);
      }
    }

    document.getElementById("checkToggle").checked = false;
  };

  const handleSelectAllToggle = (e) => {
    const all = fields.getAll();
    const selected = document.getElementById("checkToggle").checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].isSelected = selected;
      fields.push(item);
    });
  };

  const myBuyerOptions = [
    {
      label: "Seller",
      value: "seller",
    },
    {
      label: "Seller Solicitor",
      value: "sellerSolicitor",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];

  const handleClick = (e) => {
    setModalShow(true);
    setId(e);
  };

  const handleClose = () => {
    setModalShow(false);
    setId(null);
  };

  return (
    <Fragment>
      <div className="tableTheme theme-table-striped">
        {modalShow ? (
          <DefaultModal show={modalShow} onHide={() => handleClose()}>
            <div>
              <AddReport
                id={id}
                closeModal={() => handleClose()}
                onSubmit={(e) => console.log(e)}
              />
            </div>
          </DefaultModal>
        ) : (
          ""
        )}

        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>
                {" "}
                <input
                  id="checkToggle"
                  type="checkbox"
                  onChange={handleSelectAllToggle}
                />
              </th>
              <th>Report</th>
              <th>Date</th>
              <th>Prepared By</th>
              <th>Requested By</th>
              <th>Ins?</th>
              <th>Rjp?</th>
            </tr>
          </thead>
          <tbody>
            <Suspense fallback={<Spinner />}>
              {fields?.map((item, index) => (
                <Fragment key={index}>
                  <Row
                    item={item}
                    onClick={(e) => handleClick(e)}
                    index={index}
                    fields={fields}
                  />
                </Fragment>
              ))}
            </Suspense>
          </tbody>
        </Table>
      </div>
    </Fragment>
  );
};

export default FormReports;
