const SingleListing = (props) => {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(true);
  const [component, setComponent] = React.useState('Listing Details');

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleChangeComponent = (e) => {
    setComponent(e);
  }


  return (
    <div className={classes.root}>
      <CssBaseline />

      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <Divider />
        <List  className={clsx('drawer-list', {
          [classes.Vhide]: !open,
        })}  >
          {['Listing Details', 'Timeline', 'Checklist','Advertise Schedule','Library','Uploads','Book Appointments','Adjust Price','Mail Log','Seller Report','Posts','Matched Buyers','Buyer Enquires','Viewed Buyers','Sales Advice','Create Rental'].map((text, index) => (
            <ListItem onClick={()=> handleChangeComponent(text)} key={cuid()} className={clsx({ "active": stringCompare(text,component),})}>
              {text}
            </ListItem>
          ))}
        </List>
        <div  className={clsx(classes.toolbar, {
              [classes.toolbarClose]: !open,
            })}  >
          <IconButton onClick={handleDrawerClose} className={clsx(classes.menuButton, {
              [classes.hide]: !open,
            })}>
           <ChevronLeftIcon />
          </IconButton>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
           <ChevronRightIcon />
          </IconButton>
        </div>
        
      </Drawer>
      <div className={clsx(classes.content, {
              [classes.closeContent]: !open,
            })}   >
        {/* <SingleListingForm formId={component.replace(/\s/g, '')} > */}
        <SingleListingList pageComponent={component} />
        {/* </SingleListingForm> */}
      </div>
    </div>
  );
}

