import React, { Fragment, useRef, useState } from 'react'
import DefaultModal from '../../../../../../partials/modal/DefaultModal';
import ThemeHeader from '../../../../../../partials/content/ThemeHeader';
import ThemeButton from '../../../../../../partials/button/ThemeButton';
import { FieldArray } from 'redux-form';
import FormReports from '../../../../../../partials/form/partials/FormReports';
import AddReport from '../../../../modals/addReport/AddReport';
const SLReports = (props) => {
    const [modalShow, setModalShow] = useState(false);
    const [deleteItem, setDeleteItem] = useState(0);
    return (
        <Fragment>
        {modalShow ? (
          <DefaultModal show={modalShow} onHide={() => setModalShow(false)}>
            <div>
              <AddReport closeModal={() => setModalShow(false)} onSubmit={(e) => console.log(e)} />
            </div>
  
          </DefaultModal>
        ) : (
          ""
        )}
        <ThemeHeader
          right={
            <Fragment>
              <ThemeButton className="mr-2 lightGrey-button" onClick={() => setDeleteItem(prev => prev + 1)} >Remove </ThemeButton>
              <ThemeButton className="theme-button green-button" onClick={()=> setModalShow(true) }>Add</ThemeButton>
            </Fragment>
          }
        />
        <FieldArray edit={props.edit} name="reports" deleteItem={deleteItem} component={FormReports}/>
      </Fragment>
    )
}

export default SLReports
