import React, { useEffect, useState, Fragment } from "react";
import moment from "moment";
import "react-datepicker/dist/react-datepicker.css";
import ThemeHeader from "../../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../../partials/button/ThemeButton";
import DynamicTableEdit from "../../../../../partials/table/DynamicTableEdit";
import { getAllMail } from "../../../../buyers/crud/buyersCrud";
import { Chip } from "@material-ui/core";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


const SLviewedBuyersTable = ({ edit , handleChange}) => {
  const [items, setItems] = useState(null);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  // setItems(data.data);
  console.log(edit);
  useEffect(() => {
    getAllMail()
      .then(({ data }) => {
        setItems(data.data);
        console.log(data.data);
      })
      .catch(() => {});
  }, []);


  const columns = React.useMemo(
    () => [
      {
        Header: "Mail",
        columns: [
          {
            Header: "Date",
            accessor: "date",
            Cell: ({ cell: { value } }) => (
              <p>{moment(value).format("DD/MM/YYYY")}</p>
            ),
          },
          {
            Header: "Time",
            accessor: "id",
            Cell: ({
              cell: {
                row: { original },
              },
            }) => <p>{moment(original.date).format("hh : m")}</p>,
          },
          {
            Header: "By",
            accessor: "from",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
          {
            Header: "Type",
            accessor: "type",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
          {
            Header: "Subject",
            accessor: "subject",
            Cell: ({ cell: { value } }) => <p>{value}</p>,
          },
        ],
      },
    ],
    []
  );
  console.log(items);
  const fetchMoreData = () => {
    // setTimeout(() => {
    //   setItems(items.concat(items));
    // }, 1000);
  };

  const rowClick = (e) => {

    // setShowRowOnClick(!showRowOnClick)

  };


  const datas = React.useMemo(() => items, [items]);

  return (
    <div>

      <ThemeHeader
      left={
        <Fragment>
          <div>
              <DatePicker
                name="startDate"
                className="form-control"
                selected={startDate}
                onChange={(date) => setStartDate(date)}
                fixedHeight
              />
            </div>
            <div className="pl-2">
              <DatePicker
                name="endDate"
                className="form-control"
                selected={endDate}
                onChange={(date) => setEndDate(date)}
                fixedHeight
              />
            </div>
        </Fragment>
      }
        right={
          <Fragment>
              <ThemeButton
              icon
              className="mr-2 lightGrey-button-icon"
            >
              <i class="fas fa-download    "></i>{" "}
            </ThemeButton>
            <ThemeButton
              icon
              className="mr-2 lightGrey-button-icon"
            >
              <i class="fas fa-mobile    "></i>
            </ThemeButton>
            <ThemeButton
              icon
              className="mr-2 lightGrey-button-icon"
            >
              <i class="fas fa-envelope    "></i>
            </ThemeButton>
             <Chip className="mr-2" label={"Matched Buyers "+items?.length} variant="outlined" />
            <ThemeButton
                  className="lightGrey-button"
                  onClick={() => handleChange()}
                >
                  Filter
                </ThemeButton>
          </Fragment>
        }
      />
      <div className="mail-log-table">
        {datas ? 

            <DynamicTableEdit
              handleChange={(e) => console.log(e)}
              isEditable={true}
              columns={columns}
              data={datas}
              update={fetchMoreData}
              handleRowClick={rowClick}
            /> : 
          "loading"
        }
      </div>
    </div>
  );
};

export default SLviewedBuyersTable;
