const IndeterminateCheckbox = React.forwardRef(
  ({ indeterminate, ...rest }, ref) => {
    const defaultRef = React.useRef();
    const resolvedRef = ref || defaultRef;

    React.useEffect(() => {
      resolvedRef.current.indeterminate = indeterminate;
    }, [resolvedRef, indeterminate]);

    return (
      <>
        {/* e.stopPropagation() will stop it from row onClick */}
        <Checkbox className="table-checkbox" ref={resolvedRef} {...rest} />
      </>
    );
  }
);


const getItemStyle = (isDragging, draggableStyle) => ({
    // some basic styles to make the items look a bit nicer
    userSelect: 'none',
    position: 'static',

    // change background colour if dragging
    background: isDragging ? 'lightgreen' : 'grey',

    // styles we need to apply on draggables
    ...draggableStyle
});

const getListStyle = isDraggingOver => ({
    background: isDraggingOver ? 'lightblue' : 'lightgrey',
});

const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const QuoteList = React.memo(function QuoteList({ quotes }) {
  return quotes?.map((item, index) => (
    <Quote quote={item} index={index} key={index} />
  ));
});

const move = (source, destination, droppableSource, droppableDestination) => {
  const sourceClone = Array.from(source);
  const destClone = Array.from(destination);
  console.log(sourceClone);
  console.log(destClone);

  const [removed] = sourceClone.splice(droppableSource.index, 1);
  destClone.push(removed);

  //   destClone.splice(droppableDestination.index, 0, removed);

  const result = {};
  result[droppableSource.droppableId] = sourceClone;
  result[droppableDestination.droppableId] = destClone;

  return result;
};

const getRenderItem = (items) => (provided, snapshot, rubric) => (
  <div
    {...provided.draggableProps}
    {...provided.dragHandleProps}
    ref={provided.innerRef}
  >
    Item id: {items[rubric.source.index].id}
  </div>
);

function Quote({ quote, index }) {
  return (
    <Draggable draggableId={quote.id} index={index}>
      {(provided,snapshot) => (
           <tr ref={provided.innerRef}
           {...provided.draggableProps}
           {...provided.dragHandleProps}
           style={getItemStyle(
            snapshot.isDragging,
            provided.draggableProps.style
        )}
           >
           <td >{quote.advertisingItem} {quote.price} <ThemeButton icon > <i class="fas fa-bars    "></i> </ThemeButton></td>
         </tr>
      )}
    </Draggable>
  );
}

function Table({
  columns,
  data,
  update,
  handleChange,
  selectable,
  isEditable,
}) {
  // Use the state and functions returned from useTable to build your UI
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    preGlobalFilteredRows,
    prepareRow,
    selectedFlatRows,
    footerGroups,
    state: { sortBy },
  } = useTable(
    {
      columns,
      data,
    },

    isEditable
      ? (hooks) => {
          hooks.allColumns.push((columns) => [
            // Let's make a column for selection
            {
              id: "selection",
              // The header can use the table's getToggleAllRowsSelectedProps method
              // to render a checkbox.  Pagination is a problem since this will select all
              // rows even though not all rows are on the current page.  The solution should
              // be server side pagination.  For one, the clients should not download all
              // rows in most cases.  The client should only download data for the current page.
              // In that case, getToggleAllRowsSelectedProps works fine.
              Header: ({ getToggleAllRowsSelectedProps }) => (
                <div>
                  <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
                </div>
              ),
              // The cell can use the individual row's getToggleRowSelectedProps method
              // to the render a checkbox
              Cell: ({ row }) => (
                <div>
                  <IndeterminateCheckbox {...row.getToggleRowSelectedProps()} />
                </div>
              ),
            },
            ...columns,
          ]);
        }
      : "",
    useGlobalFilter, // useGlobalFilter!
    useSortBy,
    useRowSelect
  );

  React.useEffect(() => {
    console.log("sort");
  }, [sortBy]);

  console.log(rows);
  console.log(preGlobalFilteredRows);
  const dispatch = useDispatch();

  React.useEffect(() => {
    console.log(selectedFlatRows.map((d) => d.original));
    handleChange(selectedFlatRows.map((d) => d.original));
  }, [selectedFlatRows]);

  return (
    <Fragment>
      <div className="tableFixHead theme-table-striped d-flex overflow-hidden">
        <div className=" overflow-auto">
          <table className="table simple">
            <thead>
              <th>Package</th>
            </thead>
              {/* {selectable?.map((row, i) => (
                <Fragment>
                  <tr>
                    <td key={i}>{row.advertisingItem}</td>
                  </tr>
                </Fragment>
              ))} */}
              <Droppable droppableId="tableSource"
              renderClone={(provided, snapshot, rubric) => (
                // <div
                //   {...provided.draggableProps}
                //   {...provided.dragHandleProps}
                //   ref={provided.innerRef}
                // >
                //   Item id:
                // </div>
                           <tr classnames="d-flex justify-content-between" ref={provided.innerRef}
                           {...provided.draggableProps}
                           {...provided.dragHandleProps}
                           style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                        )}
                           >
                           <td >{selectable[rubric.source.index].advertisingItem} {selectable[rubric.source.index].price} <ThemeButton icon > <i class="fas fa-bars    "></i> </ThemeButton></td>
                         </tr>
              )}
              >
                {(provided, snapshot) => (
                  <tbody   style={getListStyle(snapshot.isDraggingOver)}  ref={provided.innerRef} {...provided.droppableProps}>
                      {console.log(provided)}
                      {console.log(snapshot)}
                    <QuoteList quotes={selectable} />
                    {provided.placeholder}
                  </tbody>
                )}
              </Droppable>
          </table>
        </div>

        <Droppable droppableId="tableDestination">
          {(provided) => (
            <div className="overflow-auto flex-grow-1" ref={provided.innerRef} {...provided.droppableProps}>
              <table
                {...getTableProps()}
                className="table  w-100 h-100"
              >
                <thead>
                  {headerGroups.map((headerGroup) => (
                    <tr {...headerGroup.getHeaderGroupProps()}>
                      {headerGroup.headers.map((column) => (
                        <th {...column.getHeaderProps()}>
                          {column.render("Header")}
                        </th>
                      ))}
                    </tr>
                  ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                  {rows.map((row, i) => {
                    prepareRow(row);
                    return (
                      <tr {...row.getRowProps()}>
                        {row.cells.map((cell, i) => {
                          return (
                            <td key={i} {...cell.getCellProps()}>
                              {cell.render("Cell")}
                            </td>
                          );
                        })}
                      </tr>
                    );
                  })}
                </tbody>
                <tfoot>
                  {footerGroups.map((group) => (
                    <tr {...group.getFooterGroupProps()}>
                      {group.headers.map((column) => (
                        <td {...column.getFooterProps()}>
                          {column.render("Footer")}
                        </td>
                      ))}
                    </tr>
                  ))}
                </tfoot>
              </table>
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </div>
    </Fragment>
  );
}

