function Row({ fields, item, index, onClick, edit }) {
  const [open, setOpen] = React.useState(false);
  const handleCollapse = (e) => {
    e.stopPropagation();
    setOpen(!open);
  };
  const myBuyerOptions = [
    {
      label: "Seller",
      value: "seller",
    },
    {
      label: "Seller Solicitor",
      value: "sellerSolicitor",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];

  console.log(edit);
  return (
    <React.Fragment>
        <td>
          <Field
            name={`${item}.isSelected`}
            component={RadioInput}
            type="checkbox"
          />
        </td>
        <td>{fields?.get(index)?.priority}</td>
        <td className="d-flex">
          <Field name={`${item}.task.task`} component={TextInput} edit={edit} />
          {fields?.get(index)?.task?.subTask?.length ? (
            <ThemeButton onClick={(e) => handleCollapse(e)} icon>
              {" "}
              <i class="fa fa-arrow-right" aria-hidden="true"></i>
            </ThemeButton>
          ) : (
            <ThemeButton icon onClick={(e) => handleCollapse(e)}>
              {" "}
              <i class="fas fa-plus    "></i>
            </ThemeButton>
          )}
        </td>
        <td>{fields?.get(index)?.allocation}</td>
        <td className="d-flex lastColumn" colspan="4">
          <div className="d-flex w-100">
            <Field
              name={`${item}.when.number`}
              component={TextInput}
              edit={edit}
              options = {myBuyerOptions}
            />
            <Field
              name={`${item}.when.timeType`}
              component={MultiSelect}
              edit={edit}
              isMulti={false}
              showBottom={false}
              options = {myBuyerOptions}
            />
            <Field
              name={`${item}.when.position`}
              component={MultiSelect}
              edit={edit}
              options = {myBuyerOptions}
            />
            <Field
              name={`${item}.when.type`}
              component={MultiSelect}
              edit={edit}
              options = {myBuyerOptions}
            />
            <Handle />
          </div>{" "}
        </td>

      <Fragment>
        {open ? (
          <FieldArray
            name={`${item}.task.subTask`}
            component={renderHobbies}
            id={cuid()}
          />
        ) : (
          ""
        )}

        {fields?.get(index)?.task?.subTask?.length && open ? (
          <Fragment>
            {fields?.get(index)?.task?.subTask?.map((subtask, index) => (
              <tr>
                <td className="p-2" colspan="7">
                  <Field
                    name={`${item}.task.subTask[${index}]`}
                    component={TextInput}
                    edit={edit}
                  />
                  {/* {fields?.get(index)?.task?.subTask[index]} */}
                  {/* {item.subTask} */}
                </td>
              </tr>
            ))}
          </Fragment>
        ) : (
          ""
        )}
      </Fragment>
    </React.Fragment>
  );
}

const CheckListSettingsForm = (props) => {
  const { fields, type, edit } = props;
  const [id, setId] = useState(null);

  useEffect(() => {
    getListingCheckListSetting()
      .then(({ data }) => {
        if (!fields.length > 0) {
          console.log(data);
          data.map((item, index) => {
            fields.push(item);
          });
        }
      })
      .catch(() => {});
  }, []);

  useEffect(() => {
    console.log("i caleed");
    // getListingCheckListSetting()
    //   .then(({ data }) => {
    //     fields.removeAll();
    //       data.map((item, index) => {
    //         fields.push(item);
    //       });
    //   })
    //   .catch(() => {});
  }, [type]);

  useEffect(() => {
    handleRemove();
  }, [props.deleteItem]);

  const handleAddFeature = () => {
    let obj = {};
    obj.features = document.getElementById("features").value;
    obj.comments = document.getElementById("featureComments").value;
    obj.preference = document.getElementById("featurePreference")?.value
      ? document.getElementById("featurePreference").value
      : "";
    obj.landSize = document.getElementById("featureLandsize")?.value
      ? document.getElementById("featureLandsize").value
      : "";
    obj.landType = document.getElementById("featuresLandType")?.value
      ? document.getElementById("featuresLandType").value
      : "";
    // fields.push(obj);
  };

  const handleRemove = () => {
    for (let i = fields.length - 1; i >= 0; i--) {
      if (fields.get(i).isSelected) {
        fields.remove(i);
      }
    }

    // document.getElementById("checkToggle").checked = false;
  };

  const handleSelectAllToggle = (e) => {
    const all = fields.getAll();
    const selected = document.getElementById("checkToggle").checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].isSelected = selected;
      fields.push(item);
    });
  };

  const myBuyerOptions = [
    {
      label: "Seller",
      value: "seller",
    },
    {
      label: "Seller Solicitor",
      value: "sellerSolicitor",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];

  return (
    <Fragment>
      <div className="tableTheme theme-table-striped">
        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>
                <input id="checkToggle" type="checkbox" />
              </th>
              <th>Pri.</th>
              <th>Task</th>
              <th>Allocation</th>
              <th>When</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td>
                <select
                  className="form-control"
                  id="features"
                  className="custom-select"
                >
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                </select>
              </td>
              <td>
                <input
                  type="text"
                  id="task"
                  className="  form-control fix-rounded-right"
                  required
                />
              </td>
              <td>
                <select
                  className="form-control"
                  id="allocation"
                  className="custom-select"
                >
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                </select>
              </td>
              <td className="d-flex">
                <input
                  type="date"
                  id="date"
                  className="  form-control fix-rounded-right"
                  required
                />
                <IconButton
                  onClick={handleAddFeature}
                  aria-label="delete"
                  size="small"
                  className="brand-icon-button"
                >
                  <i className="fas fa-plus-circle"></i>
                </IconButton>
              </td>
            </tr>
            </tbody>
            <Suspense fallback={<Spinner />}>
            <SortableTable fields={fields}>
              {fields?.map((item, index) => (
                <Fragment key={index}>
                  <Row item={item} index={index} fields={fields} edit={edit} />
                </Fragment>
              ))} </SortableTable>
            </Suspense>
         
        </Table>
      </div>
    </Fragment>
  );
};

export default CheckListSettingsForm;
