import React, { Fragment, useEffect, useState, useRef, Suspense } from "react";
import { Table, Form, Col, Overlay, Popover, Spinner } from "react-bootstrap";
import { Field, FieldArray } from "redux-form";
import cuid from "cuid";
import moment from "moment";
import DefaultModal from "../../../../../partials/modal/DefaultModal";
import AddReport from "../../../modals/addReport/AddReport";
import { getListingCheckListById } from "../../../_crud/listingsCrud";
import ThemeButton from "../../../../../partials/button/ThemeButton";
import RadioInput from "../../../../../partials/form/RadioInput";
import TextInput from "../../../../../partials/form/TextInput";
import DateInput from "../../../../../partials/form/DateInput";
import { IconButton } from "@material-ui/core";

const renderHobbies = ({ fields, id, meta: { error } }) => {
  const handleClick = () => {
    let obj = {};

    let val = document.getElementById(id).value;
    obj = { val };
    fields.push(val);
    console.log(obj);
  };

  return (
    <Fragment>
      <tr className="expandable" key="tr-expander">
        <td colSpan={6}>
          <div className="d-flex">
            <input class="form-control" type="text" id={id} />
            <IconButton
              onClick={() => handleClick()}
              aria-label="delete"
              size="small"
              className="brand-icon-button"
            >
              <i className="fas fa-plus-circle"></i>
            </IconButton>
          </div>
        </td>
      </tr>
    </Fragment>
  );
};

function Row({ fields, item, index, onClick, edit }) {
  const [open, setOpen] = React.useState(false);
  const handleCollapse = (e) => {
    e.stopPropagation();
    setOpen(!open);
  };

  console.log(edit);
  return (
    <React.Fragment>
      <tr>
        <td>
          <Field
            name={`${item}.isSelected`}
            component={RadioInput}
            type="checkbox"
          />
        </td>
        <td>{fields?.get(index)?.priority}</td>
        <td>
          <Field name={`${item}.task.task`} component={TextInput} edit={edit} />
          {fields?.get(index)?.task?.subTask?.length ? (
            <ThemeButton onClick={(e) => handleCollapse(e)} icon>
              {" "}
              <i class="fa fa-arrow-right" aria-hidden="true"></i>
            </ThemeButton>
          ) : (
            <ThemeButton icon onClick={(e) => handleCollapse(e)}>
              {" "}
              <i class="fas fa-plus    "></i>
            </ThemeButton>
          )}
        </td>
        <td>{fields?.get(index)?.allocation}</td>
        <td>
          {" "}
          <Field name={`${item}.date`} component={DateInput} edit={edit} />
        </td>
      </tr>

      <Fragment>
        {open ? (
          <FieldArray
            name={`${item}.task.subTask`}
            component={renderHobbies}
            id={cuid()}
          />
        ) : (
          ""
        )}

        {fields?.get(index)?.task?.subTask?.length && open ? (
          <Fragment>
            {fields?.get(index)?.task?.subTask?.map((subtask, index) => (
              <tr>
                <td className="p-2" colspan="7">
                  <Field
                    name={`${item}.task.subTask[${index}]`}
                    component={TextInput}
                    edit={edit}
                  />
                  {/* {fields?.get(index)?.task?.subTask[index]} */}
                  {/* {item.subTask} */}
                </td>
              </tr>
            ))}
          </Fragment>
        ) : (
          ""
        )}
      </Fragment>
    </React.Fragment>
  );
}

const FormCheckList = (props) => {
  const { fields, edit , task, allocation} = props;
  const [featuresValue, setFeaturesValue] = useState("");
  const [modalShow, setModalShow] = useState(false);
  const [id, setId] = useState(null);

  useEffect(() => {
    getListingCheckListById()
      .then(({ data }) => {
        if (!fields.length > 0) {
          console.log(data);
          data.map((item, index) => {
            fields.push(item);
          });
        }
      })
      .catch(() => {});
  }, []);

  useEffect(() => {
    console.log('i caleed');
    getListingCheckListById()
      .then(({ data }) => {
          fields.removeAll();
          data.map((item, index) => {
            fields.push(item);
          });
      })
      .catch(() => {});
  }, [task,allocation]);



  useEffect(() => {
    handleRemove();
  }, [props.deleteItem]);

  const handleAddFeature = () => {
    let obj = {};
    obj.features = document.getElementById("features").value;
    obj.comments = document.getElementById("featureComments").value;
    obj.preference = document.getElementById("featurePreference")?.value
      ? document.getElementById("featurePreference").value
      : "";
    obj.landSize = document.getElementById("featureLandsize")?.value
      ? document.getElementById("featureLandsize").value
      : "";
    obj.landType = document.getElementById("featuresLandType")?.value
      ? document.getElementById("featuresLandType").value
      : "";
    // fields.push(obj);
  };

  const handleRemove = () => {
    for (let i = fields.length - 1; i >= 0; i--) {
      if (fields.get(i).isSelected) {
        fields.remove(i);
      }
    }

    // document.getElementById("checkToggle").checked = false;
  };

  const handleSelectAllToggle = (e) => {
    const all = fields.getAll();
    const selected = document.getElementById("checkToggle").checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].isSelected = selected;
      fields.push(item);
    });
  };

  const myBuyerOptions = [
    {
      label: "Seller",
      value: "seller",
    },
    {
      label: "Seller Solicitor",
      value: "sellerSolicitor",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];

  const handleClick = (e) => {
    setModalShow(true);
    setId(e);
  };

  const handleClose = () => {
    setModalShow(false);
    setId(null);
  };

  return (
    <Fragment>
      <div className="tableTheme theme-table-striped">
        {modalShow ? (
          <DefaultModal show={modalShow} onHide={() => handleClose()}>
            <div>
              <AddReport
                id={id}
                closeModal={() => handleClose()}
                onSubmit={(e) => console.log(e)}
              />
            </div>
          </DefaultModal>
        ) : (
          ""
        )}

        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>
                <input
                  id="checkToggle"
                  type="checkbox"
                  onChange={handleSelectAllToggle}
                />
              </th>
              <th>Priority</th>
              <th>Task</th>
              <th>Allocation</th>
              <th>Date</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td>
                <select
                  className="form-control"
                  id="features"
                  onChange={(e) => setFeaturesValue(e.target.value)}
                  className="custom-select"
                >
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                </select>
              </td>
              <td>
                <input
                  type="text"
                  id="task"
                  className="  form-control fix-rounded-right"
                  required
                />
              </td>
              <td>
                <select
                  className="form-control"
                  id="allocation"
                  onChange={(e) => setFeaturesValue(e.target.value)}
                  className="custom-select"
                >
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                </select>
              </td>
              <td className="d-flex">
                <input
                  type="date"
                  id="date"
                  className="  form-control fix-rounded-right"
                  required
                />
                <IconButton
                  onClick={handleAddFeature}
                  aria-label="delete"
                  size="small"
                  className="brand-icon-button"
                >
                  <i className="fas fa-plus-circle"></i>
                </IconButton>
              </td>
            </tr>
            <Suspense fallback={<Spinner />}>
              {fields?.map((item, index) => (
                <Fragment key={index}>
                  <Row
                    item={item}
                    onClick={(e) => handleClick(e)}
                    index={index}
                    fields={fields}
                    edit={edit}
                  />
                </Fragment>
              ))}
            </Suspense>
          </tbody>
        </Table>
      </div>
    </Fragment>
  );
};

export default FormCheckList;
