const selector = formValueSelector("addOrUpdateReport");

const mapState = (state) => {
  console.log(state);

  const values = selector(
    state,
    "type",
    "note",
    "RequestedBy",
    "preparedBy",
    "date_conducted",
    "pi",
    "rp",
    "attachment"
  );

  return {
    reports: values,
    initialValues: {},
  }; // Foram name must have to be different
};

export default connect(
  mapState,
  null
)(reduxForm({ form: "addOrUpdateReport" })(AddReport));
