const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};

const selector = formValueSelector("adjustPrice");
const mapState = (state) => {
  const values = selector(
    state,
    "price_from",
    "price_to",
    "price_view",
    "reason"
  );
  return {
    adjustPrice: values,
  };
};
export default connect(
  mapState,
  actions
)(
  reduxForm({ form: "SalesAdviceSettings", initialValues: {}, validate })(
    SalesAdviceSettings
  )
);
