import React, { Fragment, useState } from "react";
import { makeStyles, AppBar, Tab, Tabs } from "@material-ui/core";
import Select from "react-select";
import { FieldArray } from "redux-form";
import DropzoneField from "../../../../../partials/form/DropzoneField";
import LibraryText from "./LibraryText";
function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <Fragment>{children}</Fragment>}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  appBar: {
    flexDirection: "unset",
    justifyContent: "space-between",
  },
  button: {
    margin: theme.spacing(1),
  },
  input: {
    display: "none",
  },
  rightIcon: {
    marginLeft: theme.spacing(1),
  },
}));

const Library = (props) => {
  const { handleSubmit, pristine, reset, submitting, edit } = props;
  // const { contactProfileId } = props;
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const classes = useStyles();
  const options = [
    { value: "chocolate", label: "Chocolate" },
    { value: "strawberry", label: "Strawberry" },
    { value: "vanilla", label: "Vanilla" },
  ];
  return (
    <Fragment>
      <div className={classes.root}>
        <AppBar position="static" className={"app-bar " + classes.appBar}>
          <Tabs
            className="tabs"
            variant="scrollable"
            scrollButtons="auto"
            value={value}
            onChange={handleChange}
            TabIndicatorProps={{ style: { background: "transparent" } }}
          >
            <Tab label="Images" {...a11yProps(0)} />
            <Tab label="Videos" {...a11yProps(1)} />
            <Tab label="Text" {...a11yProps(2)} />
          </Tabs>

          <div className="float-item">
            <Select options={options} />
          </div>
        </AppBar>
        <TabPanel className="p-3" value={value} index={0}>
          <div className="container">
            <FieldArray
              name="images"
              type="file"
              acceptedFileType="image/*"
              uploadType="image"
              component={DropzoneField}
            />
          </div>
        </TabPanel>
        <TabPanel className="p-3" value={value} index={1}>
          <div className="container">
            <FieldArray
              name="videos"
              type="file"
              component={DropzoneField}
              acceptedFileType="video/*"
              uploadType="video"
            />
          </div>
        </TabPanel>
        <TabPanel className="p-3" value={value} index={2}>
          <div className="container">
            <LibraryText/>
            {/* <FieldArray name="texts" component={ListingText} /> */}
          </div>
        </TabPanel>
      </div>
    </Fragment>
  );
};

export default Library;
