import React, { useEffect, useState, useCallback, Fragment } from "react";
import { connect, useDispatch } from "react-redux";
// import * as modal from '../_redux/modalRedux'
import { makeStyles, Button } from "@material-ui/core";
import { reduxForm, Field, formValueSelector } from "redux-form";

import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../partials/button/ThemeButton";
import Item from "../../../../partials/display/Item";
import { Col } from "react-bootstrap";
import TextInput from "../../../../partials/form/TextInput";
import TextArea from "../../../../partials/form/TextArea";

const validate = combineValidators({
   price_from: isRequired({ message: "The event title is required" }),
   price_to: isRequired({ message: "The category is required" }),
  reason: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  price_view: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const AdjustPrice = (props) => {
  const { handleSubmit, pristine, onHide, submitting, invalid,selectedValues, adjustPrice } = props;

  const handleAdjustPrice = () => {
      console.log(adjustPrice)
    selectedValues(adjustPrice)
  }
console.log(props)
  return (
    <Fragment>
      <div className="buyerFormModal">
        <form className="contactForm" onSubmit={handleSubmit}>
          <Fragment>
            <ThemeHeader
              left={
                <Fragment>
                  <h5 className="title">Adjust Price</h5>
                </Fragment>
              }
              right={
                <Fragment>
                  <Fragment>
                    <div className="pl-2">
                      <ThemeButton onClick={onHide}>Cancel</ThemeButton>
                    </div>
                    <div className="pl-2">
                      <ThemeButton
                        disabled={invalid || pristine || submitting}
                        onClick={()=>handleAdjustPrice()}
                        className="theme-button green-button"
                      >
                        Adjust
                      </ThemeButton>
                    </div>
                  </Fragment>
                </Fragment>
              }
            />
            <Item
              left="New Price Range"
              right={
                <Fragment>
                  <div className="row">
                    <Col>
                      <Field
                        name="price_from"
                        component={TextInput}
                        placeholder="Price From"
                      />
                    </Col>
                    <Col>
                      <Field
                        name="price_to"
                        component={TextInput}
                        placeholder="Price To"
                      />
                    </Col>
                  </div>
                </Fragment>
              }
            />

            <Item
              left="New Price View"
              right={
                <Field
                  name="public_view"
                  component={TextInput}
                  placeholder="Price To"
                />
              }
            />

            <Item
              left="Reason"
              right={
                <Field
                  name="reason"
                  rows={4}
                  component={TextArea}
                  placeholder="Price To"
                />
              }
            />
          </Fragment>
        </form>
        <div>
          <Item left="past Adjustments" />
        </div>
      </div>
    </Fragment>
  );
};

const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};

const selector = formValueSelector("adjustPrice");
const mapState = (state) => {
    const values = selector(state, "price_from", "price_to", "price_view","reason");
  return {
    adjustPrice:values,
  
};}
export default connect(
  mapState,
  actions
)(reduxForm({ form: "adjustPrice", initialValues: {}, validate })(AdjustPrice));
